// /components/HomeFeatureSection :
import React, { useEffect, useRef } from "react";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import CTAButton from "../components/CTAButton";
import SectionWrapper from "../components/SectionWrapper";

if (typeof window !== "undefined") {
  gsap.registerPlugin(ScrollTrigger);
}

// ‚úÖ **kleuren van de <h1/>**
const titleColors = ["text-purple-900", "text-blue-600", "text-red-500"];

// ‚úÖ **Feature-secties array opnieuw toegevoegd**
const features = [
  {
    titleKey: "feature.title_1",
    subtitleKey: "feature.subtitle_1",
    descriptionKey: "feature.description_1",
    video: "/images/Featurevideo1.webm",
    image: "/images/Featureimage1.jpeg",
  },
  {
    titleKey: "feature.title_2",
    subtitleKey: "feature.subtitle_2",
    descriptionKey: "feature.description_2",
    video: "/images/Featurevideo2.webm",
    image: "/images/Featureimage2.jpeg",
  },
  {
    titleKey: "feature.title_3",
    subtitleKey: "feature.subtitle_3",
    descriptionKey: "feature.description_3",
    video: "/images/Featurevideo3.webm",
    image: "/images/Featureimage3.jpeg",
  },
];

const HomeFeatureSection = () => {
  console.log("Rendering [componentnaam]", Date.now());  // üöÄ Debugging

  const { t } = useTranslation();
  const sectionsRef = useRef([]); // ‚úÖ Array refs voor GSAP

  useEffect(() => {
    if (typeof window === "undefined") return; // üöÄ Voorkomt SSR-fouten
  
    gsap.registerPlugin(ScrollTrigger);
  
    const sections = sectionsRef.current.filter(Boolean); // ‚úÖ Filter lege refs
  
    if (!sections.length) return;
  
    sections.forEach((section) => {
      if (!section) return;
      
      const textBlock = section.querySelector(".feature-text");
      const image = section.querySelector(".feature-image");
      const video = section.querySelector(".feature-video");
      const description = section.querySelector(".feature-description");
      const ctaButton = section.querySelector(".feature-cta");
  
      // ‚úÖ **Tekst fade-in (titel, subtitel en description)
      gsap.fromTo(
        textBlock,
        { opacity: 0, y: 40 }, // üîπ Startpositie: xx px beneden zijn originele positie
        {
          opacity: 1,// üîπ Eindstatus: volledig zichtbaar
          y: 0, // üîπ Eindpositie: 0 = originele positie
          duration: 2, // üîπ Tijd (sec) om van zijn start- naar zijn eindpositie te gaan
          ease: "power2.out", // üîπ power2.out : zachte overgang met trage stop
          scrollTrigger: {
            trigger: textBlock,
            start: "top 60%", // üîπ Start wanneer de bovenkant van het tekstblok xx% van het schermhoogte heeft bereikt
            toggleActions: "play none none reverse",
          },
        }
      );
  
      // ‚úÖ **Description fade-in
      gsap.fromTo(
        [description, ctaButton],
        { opacity: 0, y: 40 }, // üîπ Unieke startwaarden
        {
          opacity: 1,
          y: 0,
          duration: 1,
          ease: "power2.out",
          delay: 0.8, // üîπ begint met zijn animatie x sec later dan titel/subtitel
          scrollTrigger: {
            trigger: textBlock,
            start: "top 55%",
            toggleActions: "play none none reverse",
          },
        }
      );
  
      // ‚úÖ **Afbeelding fade-in
      gsap.set(image, { opacity: 1, y: 200 });
      gsap.to(image, {
        opacity: 1,
        y: -100,
        ease: "none",
        scrollTrigger: {
          trigger: section,
          start: "top bottom",
          end: "bottom top",
          scrub: true,
        },
      });

      // ‚úÖ **video fade-in
      gsap.set(video, { opacity: 1, y: 300 });
      gsap.to(video, {
        opacity: 1,
        y: -200,
        ease: "none",
        scrollTrigger: {
          trigger: section,
          start: "top bottom",
          end: "bottom top",
          scrub: true,
        },
      });
    });
  
    return () => sections.forEach((section) => ScrollTrigger.getById(section)?.kill());
  }, []);

  return (
    <div className="">
      {features.map((feature, index) => (
        <SectionWrapper bgColor="bg-white" key={index}>
          <div
            ref={(el) => (sectionsRef.current[index] = el)} // ‚úÖ **Callback-ref**
            className="relative flex flex-col items-center text-center gap-4 overflow-hidden"
          >
            {/* ‚úÖ Tekstsectie */}
            <div className="feature-text w-full max-w-3xl">
            <h1 className={`text-lg font-semibold mb-3 ${titleColors[index]}`}>{t(feature.titleKey)}</h1>
              <h2 className="text-3xl text-gray-900 font-normal mb-3">{t(feature.subtitleKey)}</h2>
              <p className="feature-description text-lg font-light text-gray-500 mt-2">{t(feature.descriptionKey)}</p>
            {/* ‚úÖ CTA-Knop */}
            <div className="feature-cta mt-8 mb-16">
          <CTAButton />
        </div>
        </div>

            {/* ‚úÖ Media: Video en Afbeelding */}
            <div className="relative w-full flex flex-col lg:flex-row justify-center items-center">
  {/* Video links */}
  <div className="relative w-full lg:w-[65%] z-10"> {/* lg=%van totale lengte, zie afb */}
    <video
      src={feature.video}
      autoPlay
      loop
      muted
      playsInline
      className="feature-video w-full rounded-lg shadow-lg"
    />
  </div>

  {/* Afbeelding rechts, overlappend op grotere schermen */}
  <div className="relative w-full lg:w-[35%] lg:-ml-[10%] mt-4 lg:mt-0 hidden lg:block"> {/* lg:-ml = overlap */}
    <img
      src={feature.image}
      alt="Feature"
      className="feature-image w-full rounded-lg shadow-lg"
    />
  </div>
</div>
          </div>
        </SectionWrapper>
      ))}
    </div>
  );
};

export default HomeFeatureSection;
